.Navbar{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 5px;
    margin-bottom: 20px;
    height: 25px;
    padding-top: 12px;
}

.Navbar-left{
    display: flex;
    justify-content: flex-start;
    margin-left: 20px;
}

.Navbar-left .nav-links{
    display: flex;
    font-size: 0.75rem;
    margin-left: 25px;
    margin-top: 10px;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.nav-links p{
    margin-left: 20px;
}

.Navbar-left .nav-img{
    height: 40px;
    width: 100px;
}

.nav-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Navbar-right{
    display: flex;
    justify-content: flex-end;
    width: 80px;
    margin-left: 55%;
}

.Navbar-right .search{
    border: 1px solid #ccc;
    display: flex;
    border-radius: 5px;
    padding: 5px 12px;
}

.Navbar-right .search-img{
    width: 15px;
    height: 18px;
   object-fit: cover;
    margin-top: 5px;
    margin-right: 10px;
}

.search input{
    border: none;
    font-size: 0.75rem;
    outline: none;
    width: 300px;
    height: 25px;
}

.menu-button {
    display: none; /* Hide the menu button by default */
    font-size: 24px; /* Size of the menu icon */
    background: none;
    border: none;
    cursor: pointer;
    margin-left: 50px;
}

@media (max-width: 950px) {
    .Navbar-left .nav-links {
        display: none; 
        flex-direction: column; 
        position: absolute; 
        top: 40px; 
        border-radius: 6px;
        left: 0;
        background: linear-gradient(90deg, #044c5eef 0%, #636AE84D 100%);;
        width: 92%; 
        height: 50%;
        color: white;
        gap: 1.5rem;
        z-index: 10; 
        font-size: 0.98rem;
        font-weight: bold;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }

    
    .Navbar-left .nav-links.active {
        display: flex; /* Show links when active */
        padding: 20px;
    }

     .Navbar-left .nav-links.active p:hover {
        display: flex; /* Show links when active */
        width: 100%;
        justify-content: center;
        align-items: center;
        color: #E8618C4D; 
        padding: 10px;
        cursor: pointer;
        border-radius: 6px;
        background: #fff;
    }

    
    .menu-button {
        display: block; /* Show the menu button */
    }

    .Navbar-right .search {
        margin-right: 0; /* Reset margin on smaller screens */
        flex-grow: 0; /* Prevent search bar from growing */
    }

    .search input {
        width: 150px; /* Fixed width for search input */
    }
}

@media (max-width: 480px) {
    .search input {
        width: 100px; /* Further reduce width for very small screens */
    }

    .Navbar-right {
        justify-content: flex-start; /* Align search and button to the left */
    }
}
