.Latest {
    padding: 30px;
}

.Latest .Lat-top {
    display: flex;
    justify-content: space-between;
    align-items: center; /* Center items vertically */
}

.Latest .Lat-view {
    display: flex;
    cursor: pointer;
}

.Latest .Lat-view img {
    width: 15px;
    height: 15px;
    object-fit: cover;
    margin-top: 28px;
}

.Latest .Lat-view p {
    padding: 0 12px;
    font-size: 11px;
    line-height: 22px;
    font-weight: bold;
    color: #E8618CFF;
    margin-top: 20px;
}

.Latest .Lat-top h3 {
    font-size: 2em; /* Responsive font size */
    font-weight: 700;
}

.Latest .Articles {
    margin-top: 30px;
    display: flex;
    /* grid-template-columns: repeat(4, 1fr); */
    flex-wrap: wrap;
    gap: 20px;
}

.Latest .Article {
    width: 100%; /* Full width on smaller screens */
    max-width: 300px; /* Max width for larger screens */
    height: auto; /* Auto height */
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 0 1px 1px #ccc;
}

.Latest img {
    width: 100%; /* Full width for responsiveness */
    height: auto; /* Maintain aspect ratio */
    border-radius: 5px 5px 0 0;
    object-fit: cover;
}

.Latest .top-writeup {
    font-size: 0.7em; /* Responsive font size */
    color: #E8618CFF;
    padding: 6px 7px;
    margin-top: 10px;
    margin-left: 20px;
    margin-bottom: 10px;
    border-radius: 15px;
    width: fit-content;
    height: 28px;
    font-weight: bold;
    border: 1px solid #E8618CFF;
}

.Latest .art-cap {
    font-weight: 600;
    font-size: 0.95em; /* Responsive font size */
    color: #242524FF;
    margin-left: 20px;
}

.Latest .exp {
    font-size: 0.7em; /* Responsive font size */
    width: 90%; /* Use percentage for responsiveness */
    font-weight: 400;
    margin-left: 20px;
    margin-top: 5px;
    color: #8c8D8BFF;
    line-height: 15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Latest .artdet {
    display: flex;
    justify-content: flex-start;
    margin-left: 40px;
    margin-top: 10px;
    color: #8c8D8BFF;
    gap: 2rem;
}

.Latest .artdet p {
    font-size: 0.65em; /* Responsive font size */
    line-height: 12px;
    font-weight: 400;
}

.Latest .read {
    display: flex;
}

.Latest .read img {
    width: 13px;
    height: 13px;
    object-fit: cover;
    margin-top: 38px;
    line-height: 20px;
}

.Latest .readmore {
    padding: 0 12px;
    font-size: 0.9em; /* Responsive font size */
    line-height: 20px;
    color: #E8618CFF;
    margin-top: 30px;
    margin-left: 20px;
}

/* Media Queries for Responsive Design */
@media (max-width: 950px) {
    .Latest .Lat-top h3 {
        font-size: 1.6em; /* Smaller heading size */
    }
.Latest .Lat-view img {
   display: none;
}

    .Latest .read img {
    display: none;
}

    .Latest .Articles {
        grid-template-columns: repeat(2, 1fr); /* 2 columns on smaller screens */
    }

    .Latest .article {
      width: 100%;
    }

    .Latest .art-cap {
        font-size: 0.7em; /* Smaller font size for article caption */
    }

    .Latest .exp {
        font-size: 0.65em; /* Smaller font size for experience text */
    }
}

@media (max-width: 480px) {
    .Latest .Lat-top h3 {
        font-size: 1.5em; /* Further reduce heading size */
    }

    .Latest .artdet p {
        font-size: 0.7em; /* Smaller font size for article details */
    }

    .Latest .Articles {
        grid-template-columns: repeat(1, 1fr); /* 2 columns on smaller screens */
    }

    .Latest .readmore {
        font-size: 0.55em; /* Smaller read more font size */
    }
}